<template>
    <div class="edit-message">
         <h2 style="margin-bottom: 30px;font-size: 30px;">{{$route.name == '编辑回复消息' ? '编辑回复消息' : '新建回复消息'}}</h2>
        <Form class="form-group" label-position="right" :label-width="80">
            <FormItem label="类型" prop="type">
                <RadioGroup v-model="type" type="button" @on-change="change">
                    <Radio :label="0">文本</Radio>
                    <Radio :label="1">图文</Radio>
                </RadioGroup>
            </FormItem>
            <Form :model="subForm" label-position="right" :label-width="80" v-if="type == 1">
                <FormItem label="图片" prop="picurl">
                    <Upload action="http://test.oorggt.top/reply/upload" :show-upload-list="false" name="imageFile" :on-success="handleSuccess">
                        <Button icon="ios-cloud-upload-outline">点击上传</Button>
                        <br />
                        <img v-if="imgUrl" :src="imgUrl" alt="" width="200" style="margin-top:10px;">
                    </Upload>
                </FormItem>
                <FormItem label="标题" prop="title">
                    <Input size="large" type="text" v-model="subForm.title" placeholder="请输入标题"></Input>
                </FormItem>
                <FormItem label="链接" prop="url">
                    <Input size="large" type="text" v-model="subForm.url" placeholder="请输入链接"></Input>
                </FormItem>
                <FormItem label="详细信息" prop="description">
                    <Input size="large" type="textarea" v-model="subForm.description" placeholder="请输入详细信息"></Input>
                </FormItem>
                    <Button class="edit-btn" size="large" type="success" @click="addOne">添加</Button>
                    <Button class="edit-btn" size="large" type="primary" @click="saveOne">保存</Button>
                <FormItem label="图文消息">
                    <Table class="edit-message-table" stripe :columns="editHeader" :data="editData"></Table>
                </FormItem>
            </Form>
            <FormItem v-else-if="type == 0" label="详细信息" prop="description">
                <Input size="large" type="textarea" v-model="description" placeholder="请输入详细信息"></Input>
            </FormItem>
            <FormItem>
                <Button class="edit-btn" size="large" type="success" @click="onSave">保存</Button>
                <Button class="edit-btn" size="large" type="warning" @click="cancel">取消</Button>
            </FormItem>
        </Form>
    </div>
</template>

<script>
export default {
  data() {
    return {
      type: 0,
      subForm: {},
      contents: [],
      description: "",
      imgUrl: "",
      id: "",
      index: 0,
      editData: [],
      editHeader: [
        {
          key: "title",
          title: "标题",
          align: "center"
        },
        {
          key: "picurl",
          title: "图片",
          align: "center",
          render: (h, params) => {
            return h("img", {
              attrs: {
                src: params.row.picurl,
                style: "width:100%;padding:10px;"
              }
            });
          }
        },
        {
          key: "url",
          title: "链接",
          align: "center"
        },
        {
          key: "title",
          title: "详细信息",
          align: "description",
          width: 100
        },
        {
          title: "Action",
          align: "center",
          render: (h, params) => {
            let that = this;
            return h("div", [
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.index = params.index;
                      that.subForm = params.row;
                      that.imgUrl = params.row.picurl;
                    }
                  }
                },
                "修改"
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    size: "small"
                  },
                  on: {
                    click() {
                      if (
                        that.$route.name == "messageUpdate" &&
                        that.editData.length <= 1
                      ) {
                        that.$Message.info("列表中最少展示一条数据");
                      } else {
                        that.editData.splice(params.index, 1);
                        that.$Message.info("删除成功");
                      }
                    }
                  }
                },
                "删除"
              )
            ]);
          }
        }
      ]
    };
  },
  mounted() {
    if (this.$route.name == "编辑回复消息") {
      this.data_info = this.$route.params;
      this.type = this.data_info.type;
      this.description = this.data_info.description;
      this.editData = this.data_info.contents;
      this.subForm = this.editData[0];
      this.imgUrl = this.subForm.picurl;
      this.id = this.data_info._id;
    }
  },
  methods: {
    onSave() {
      if (this.$route.name == "创建回复消息") {
        if (this.description != "") {
          this.contents.push({ description: this.description });
        } else {
          this.contents = this.editData;
        }
        this.$axios
          .post("/msg/create", {
            type: this.type,
            description: this.description,
            contents: this.contents
          })
          .then(res => {
            this.reset();
            this.$Message.info("创建成功");
            window.location.href = "/manage/gonghao/message";
          });
      } else if (this.$route.name == "编辑回复消息") {
        if (this.description != "") {
          this.contents.push({ description: this.description });
        } else {
          this.contents = this.editData;
        }
        this.$axios
          .post("/msg/update", {
            id: this.id,
            type: this.type,
            description: this.description,
            contents: this.contents
          })
          .then(res => {
            this.reset();
            this.$Message.info("修改成功");
            window.location.href = "/manage/gonghao/message";
          });
      }
    },
    addOne() {
      this.subForm.picurl = this.imgUrl;
      this.editData.push(this.subForm);
      this.subForm = {};
      this.imgUrl = "";
    },
    saveOne() {
      this.subForm.picurl = this.imgUrl;
      this.editData.splice(this.index, 1, this.subForm);
      this.subForm = {};
      this.imgUrl = "";
    },
    change() {
      this.description = "";
      this.subForm = {};
    },
    handleSuccess(res, file) {
      this.imgUrl = "http://test.oorggt.top/uploads/" + res.filename;
    },
    cancel() {
      this.$router.push({ name: "素材管理" });
      this.reset();
    },
    reset() {
      this.type = 0;
      this.imgUrl = this.description = "";
      this.subForm = {};
      this.editData = [];
    }
  }
};
</script>

<style scoped>
.edit-message {
  width: 500px;
  float: left;
  padding: 20px;
}
.tag-name {
  width: 100px;
  height: 30px;
  line-height: 30px;
  border: 1px solid #eee;
  border-radius: 5px;
  padding-left: 10px;
}
.tag-list li {
  float: left;
  margin-right: 10px;
  cursor: pointer;
}
.edit-message-table {
  width: 200%;
}
.edit-btn {
  margin-right: 20px;
  margin-bottom: 30px;
}
</style>

